<template><section>
<h2>base-message-bottom</h2>
<p>介绍base-message-bottom的使用</p>
<h4>基础用法</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;base-button @click=\&quot;$refs.bottomModal.open()\&quot;&gt;点击弹出底部弹窗 当前点击了{{status}}&lt;/base-button&gt;\n    &lt;base-message-bottom ref=\&quot;bottomModal\&quot; @change=\&quot;onChangeMessage\&quot;&gt;\n      &lt;div&gt;你好&lt;/div&gt;\n      &lt;div&gt;我是插槽&lt;/div&gt;\n    &lt;/base-message-bottom&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      status: '没点'\n    }\n  },\n  methods: {\n    onChangeMessage (res) {\n      this.status = res.text\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <base-button @click="$refs.bottomModal.open()">点击弹出底部弹窗 当前点击了{{status}}</base-button>
    <base-message-bottom ref="bottomModal" @change="onChangeMessage">
      <div>你好</div>
      <div>我是插槽</div>
    </base-message-bottom>
  </div>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
