    <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"$refs.bottomModal.open()"</span>&gt;</span>点击弹出底部弹窗 当前点击了{{status}}<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">base-message-bottom</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"bottomModal"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChangeMessage"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>你好<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>我是插槽<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">base-message-bottom</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">status</span>: <span class="hljs-string">'没点'</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    onChangeMessage (res) {
      <span class="hljs-keyword">this</span>.status = res.text
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  data () {
    return {
      status: '没点'
    }
  },
  methods: {
    onChangeMessage (res) {
      this.status = res.text
    }
  }
}
</script>


